a {
    text-decoration: none;
}

.page-footer {
    background-color: var(--primary-bg-color);

   
    
    font-weight: 700;
    font-size: 18px;
    line-height: 1.2;
    
    
    color: var(--primary-text-color);

    padding-bottom: 62px;

}

.page-footer--flex {
    display: block;
    flex-direction: column;

    width: 100%;
}



.page-footer__text {
        text-transform: uppercase;

        width: 243px;
        margin-bottom: 16px;
}

.page-footer__text--big {
       
        font-size: 34px;
        letter-spacing: 0.04em;
        text-transform: uppercase;

        width: 200px;
        margin-bottom: 42px;
}



.page-footer__social-list {
    display: flex;
    gap: 30px;
    justify-content: end;

    margin-bottom: 38px;

}

.social-list__item {
   
    }


.social-list__link {
    display: flex;
    justify-content: center;
    align-items: center;
    
    width: 34px;
    height: 34px;
    color: var(--primary-bg-color);
    background-color: var(--accent-color);
    border-radius: 50%;
    

    transition: height, width var(--transition-dur-and-func);

    &:hover {
        color: var(--primary-text-color);
        height: 32px;
        width: 32px;
    }

    &:focus {
        color: var(--primary-text-color);
         height: 35px;
         width: 35px;
         
    }
}

.social-list__svg {
    fill: currentColor;
    width: 35px;
    height: 35px;
}

.page-footer__contact-text {
       
}

.page-footer__link {
        font-size: 12px;
        line-height: 1.8;

        
        color: var(--primary-text-color);

        margin-bottom: 40px;

       

}

.page-footer__contacts {
    display: flex;
    flex-direction: column;
    
    align-items: end;
}

.page-footer__link--black {
    color: var(--secondary-text-color);
}

.copyright {
        font-size: 14px;
        line-height: 1.5;
        
    
        text-align: center;
    
        color: var(--copyright-text-color);

        display: flex;
        justify-content: center;

        width: 165px;

        margin-left: auto;
        margin-right: auto;

}

.hr {
    border: 1px solid #E1E1E1;
    margin-top: 0px;
    margin-bottom: 16px;
}



@media screen and (min-width: 768px) {
    .page-footer {
            font-size: 26px;
            line-height: 1.2;
        

            color: var(--primary-text-color);
        
            padding-bottom: 9px;
    }

    .page-footer--flex {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
    }

    .page-footer__text {
            text-transform: uppercase;
    
            margin-bottom: 9px;

            width: 352px;
    }
    
    .page-footer__text--big {
    
            font-size: 48px;
            
            text-transform: uppercase;
    
            margin-bottom: 32px;

            width: 340px;
    }

    .page-footer__social-list {
        
    
        margin-top: 66px;
    
        }


    .page-footer__link {
        font-size: 14px;
        line-height: 1.8;
    
    
        text-align: right;
    
            
    
        margin-bottom: 56px;
    
        }





    .copyright{
        width: 100%;
    }

        .hr {
            border: 1px solid #E1E1E1;
            margin-bottom: 16px;
            margin-top: 0px;
        }
}

@media screen and (min-width: 1200px) {
    .page-footer {
        font-size: 26px;
        line-height: 1.2;


        color: #FCFCFC;

        padding-bottom: 9px;
    }

    .page-footer--flex {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .page-footer__text {
        text-transform: uppercase;

        margin-bottom: 9px;

        width: 504px;
    }

    .page-footer__text--big {

        font-size: 48px;

        text-transform: uppercase;

        margin-bottom: 32px;

        width: 504px;
    }

    .page-footer__social-list {


        margin-top: 66px;

    }


    .page-footer__link {
        font-size: 14px;
        line-height: 1.8;


        text-align: right;



        margin-bottom: 56px;

    }





    .copyright {
        width: 100%;
    }

    .hr {
        border: 1px solid #E1E1E1;
        margin-bottom: 16px;
        margin-top: 0px;
    }
}